import React, { useState } from 'react'
import { AppBar, Box, Button, Tab, Tabs, Toolbar, Typography } from "@mui/material"
import { Link } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { authActions } from '../store';
import { useStyles } from './Utils';
import toast from 'react-hot-toast';

const Header = () => {
  const classes = useStyles();
  const dispatch = useDispatch();
  const isLoggedIn = useSelector((state) => state.isLoggedIn);
  const [value, setValue] = useState(0);
  return (
    <AppBar position='sticky' sx={{ background: "linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%)" }}>
      <Toolbar>
        <Typography className={classes.font} variant='h5'>Blogs App</Typography>
        {isLoggedIn && <Box display="flex" sx={{ marginLeft: "auto" }}>
          <Tabs sx={{ marginLeft: "auto" }} textColor='inherit' value={value} onChange={(e, val) => setValue(val)}>
            <Tab className={classes.font} LinkComponent={Link} to="/blogs" label="All Blogs" />
            <Tab className={classes.font} LinkComponent={Link} to="/myBlogs" label="My Blogs" />
            <Tab className={classes.font} LinkComponent={Link} to="/blogs/add" label="Add Blog" />
          </Tabs>
        </Box>}
        <Box display="flex" sx={{ marginLeft: "auto" }}>
          {!isLoggedIn &&
            <>
              <Button className={classes.font} LinkComponent={Link} to="/auth" variant='contained' sx={{ margin: 1, borderRadius: 10, backgroundColor: "white", color: "red" }}>Login</Button>
              <Button className={classes.font} LinkComponent={Link} to="/auth" variant='contained' sx={{ margin: 1, borderRadius: 10, backgroundColor: "white", color: "red" }}>Signup</Button>
            </>
          }
          {isLoggedIn && <Button className={classes.font} onClick={() => {
            toast.success("User Logout")
            dispatch(authActions.logout())

          }} LinkComponent={Link} to="/auth" variant='contained' sx={{ margin: 1, borderRadius: 10, backgroundColor: "white", color: "red" }}>Logout</Button>}
        </Box>
      </Toolbar>
    </AppBar>
  )
}

export default Header