import { Box, Button, InputLabel, TextField, Typography } from '@mui/material'
import axios from 'axios';
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { useStyles } from './Utils';
import { API_URL_BLOG } from './Api';

const labelStyle = { mb: -2, mt: 2, fontSize: "15px", fontWeight: "bold" };

const AddBlog = () => {
  const classes = useStyles();
  const navigate = useNavigate();
  const [inputs, setInputs] = useState({
    title: "", description: "", image: ""
  })
  const handleChange = (e) => {
    setInputs((prev) => ({
      ...prev, [e.target.name]: e.target.value
    }))
  };

  const sendRequest = async (type = "login") => {
    const response = await axios.post(`${API_URL_BLOG}/add`, {
      title: inputs.title,
      description: inputs.description,
      image: inputs.image,
      user: localStorage.getItem("userId")
    }).catch((err) => console.log(err));
    const data = await response.data;
    // console.log(data);
    return data;
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    // console.log(inputs);
    sendRequest().then(() => navigate("/")).then(() => navigate("/blogs"));
  }
  return (
    <div>
      <form onSubmit={handleSubmit}>
        <Box
          border={3}
          borderColor="linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%)"
          borderRadius={10}
          boxShadow="10px 10px 20px #ccc"
          padding={3}
          margin={"auto"}
          marginTop={4}
          display="flex"
          flexDirection="column"
          width="70%"
        >
          <Typography className={classes.font} fontWeight={"bold"} paddingTop={2} color="gray" variant='h4' textAlign={"center"}>Post Your Blog</Typography>
          <InputLabel className={classes.font} sx={labelStyle}>Title</InputLabel>
          <TextField className={classes.font} name="title" value={inputs.title} onChange={handleChange} margin='normal' variant='outlined' />
          <InputLabel className={classes.font} sx={labelStyle} >Description</InputLabel>
          <TextField className={classes.font} name='description' value={inputs.description} onChange={handleChange} margin='normal' variant='outlined' />
          <InputLabel className={classes.font} sx={labelStyle} >Image URL</InputLabel>
          <TextField className={classes.font} name="image" value={inputs.image} onChange={handleChange} margin='normal' variant='outlined' />
          <Button
            type='submit'
            variant='contained'
            color='primary'
            sx={{ borderRadius: 3, marginTop: 1 }}
          > Submit
          </Button>
        </Box>
      </form>
    </div>
  )
}

export default AddBlog