import React from 'react'
import { Avatar, Box, Card, CardContent, CardHeader, CardMedia, IconButton, Typography } from '@mui/material'
import EditIcon from '@mui/icons-material/Edit';
import DeleteForeverIcon from '@mui/icons-material/DeleteForever';
import { useNavigate } from 'react-router-dom';
import axios from 'axios';
import { useStyles } from './Utils';
import { API_URL_BLOG } from './Api';

const Blog = ({ title, description, imageURL, userName, isUser, id }) => {
    // console.log(title, isUser);
    const classes = useStyles();
    const navigate = useNavigate();
    const handleEdit = (e) => {
        navigate(`/myBlogs/${id}`)
    }
    const deleteRequest = async () => {
        const response = await axios.delete(`${API_URL_BLOG}/${id}`).catch((err) => console.log(err));
        const data = await response.data;
        return data;
    }
    const handleDelete = (e) => {
        deleteRequest().then(() => navigate("/")).then(() => navigate("/blogs"));
        window.location.reload();
    }
    return (
        <div>
            <Card sx={{
                width: "40%", margin: "auto", marginTop: 2, padding: 2, boxShadow: "5px 5px 10px #ccc",
                ":hover": { boxShadow: "10px 10px 20px #ccc" }, cursor: "pointer"
            }}>

                <CardHeader className={classes.font}
                    avatar={
                        <Avatar className={classes.font} sx={{ bgcolor: "red" }} aria-label="recipe">
                            {userName.charAt(0).toUpperCase()}
                        </Avatar>

                    }
                    title={title}
                // subheader="September 14, 2023"
                />

                {isUser && (
                    <Box className={classes.font} display="flex" sx={{ mt: -7, mb: 4 }}>
                        <IconButton
                            onClick={handleEdit}
                            sx={{ marginLeft: "auto" }}
                        > <EditIcon color='warning' />
                        </IconButton>
                        <IconButton
                            onClick={handleDelete}
                        > <DeleteForeverIcon color='error' />
                        </IconButton>
                    </Box>
                )}

                <CardMedia className={classes.font}
                    component="img"
                    height="300"
                    // height="194"
                    image={imageURL}
                    alt="Paella dish"
                />
                <CardContent>
                    <hr />
                    <br />
                    <Typography className={classes.font} variant="body2" color="text.secondary">
                        <b>{userName}</b>{": "} {description}
                    </Typography>
                </CardContent>
            </Card>
        </div>
    )
}

export default Blog