import { Box, Button, InputLabel, TextField, Typography } from '@mui/material';
import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { useNavigate, useParams } from 'react-router-dom';
import { API_URL_BLOG } from './Api';

const labelStyle = { mb: -2, mt: 2, fontSize: "15px", fontWeight: "bold" };

const BlogDetail = () => {
  const navigate = useNavigate();
  const id = useParams().id;
  // eslint-disable-next-line
  const [blog, setBlog] = useState();
  // console.log(id);
  const [inputs, setInputs] = useState({})
  const handleChange = (e) => {
    setInputs((prev) => ({
      ...prev, [e.target.name]: e.target.value
    }))
  };
  const fetchDetails = async () => {
    const response = await axios.get(`${API_URL_BLOG}/${id}`).catch((err) => console.log(err));
    const data = await response.data;
    return data;
  }
  useEffect(() => {
    fetchDetails().then((data) => {
      setBlog(data.blog)
      setInputs({
        title: data.blog.title,
        description: data.blog.description,
        image: data.blog.image
      })
    });
    // eslint-disable-next-line
  }, [id]);

  const sendRequest = async () => {
    const response = await axios.put(`${API_URL_BLOG}/update/${id}`, {
      title: inputs.title,
      description: inputs.description,
      image: inputs.image
    }).catch((err) => console.log(err));
    const data = await response.data;
    return data;
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    // console.log(inputs);
    sendRequest().then((data) => console.log(data)).then(() => navigate("/myBlogs"));
  }

  // console.log(blog);
  return (
    <div>
      {inputs && <form onSubmit={handleSubmit}>
        <Box
          border={3}
          borderColor="linear-gradient(90deg, rgba(2,0,36,1) 0%, rgba(9,9,121,1) 35%, rgba(0,212,255,1) 100%)"
          borderRadius={10}
          boxShadow="10px 10px 20px #ccc"
          padding={3}
          margin={"auto"}
          marginTop={4}
          display="flex"
          flexDirection="column"
          width="70%"
        >
          <Typography fontWeight={"bold"} paddingTop={2} color="gray" variant='h4' textAlign={"center"}>Post Your Blog</Typography>
          <InputLabel sx={labelStyle}>Title</InputLabel>
          <TextField name="title" value={inputs.title} onChange={handleChange} margin='normal' variant='outlined' />
          <InputLabel sx={labelStyle} >Description</InputLabel>
          <TextField name='description' value={inputs.description} onChange={handleChange} margin='normal' variant='outlined' />
          <InputLabel sx={labelStyle} >Image URL</InputLabel>
          <TextField name="image" value={inputs.image} onChange={handleChange} margin='normal' variant='outlined' />
          <Button
            type='submit'
            variant='contained'
            color='primary'
            sx={{ borderRadius: 3, marginTop: 1 }}
          > Submit
          </Button>
        </Box>
      </form>}
    </div>
  )
}

export default BlogDetail