import React, { useState } from 'react'
import { Box, Button, TextField, Typography } from '@mui/material'
import axios from "axios";
import { useDispatch } from 'react-redux';
import { authActions } from '../store';
import { useNavigate } from 'react-router-dom';
import { API_URL_USER } from './Api';
import toast from 'react-hot-toast';

const Auth = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch();
  const [inputs, setInputs] = useState({
    name: "", email: "", password: ""
  })
  const [isSignup, setIsSignup] = useState(false);

  const handleChange = (e) => {
    setInputs((prev) => ({
      ...prev, [e.target.name]: e.target.value
    }))
  };

  const sendRequest = async (type = "login") => {
    const response = await axios.post(`${API_URL_USER}/${type}`, {
      name: inputs.name,
      email: inputs.email,
      password: inputs.password
    }).catch((err) => console.log(err));
    const data = await response.data;
    // console.log(data);
    return data;
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    // console.log(inputs);
    if (isSignup) {
      sendRequest("signup").then((data) => localStorage.setItem("userId", data.user._id)).then(() => dispatch(authActions.login())).then(() => navigate("/blogs"))
        .then((data) => console.log(data));
        toast.success("User Sign up Successfully");
    } else {
      sendRequest().then((data) => localStorage.setItem("userId", data.user._id)).then(() => dispatch(authActions.login())).then(() => navigate("/blogs"))
        .then((data) => console.log(data));
        toast.success("User Login Successfully");
    }
  }
  return (
    <div>
      <form onSubmit={handleSubmit}>
        <Box
          display="flex"
          flexDirection="column"
          alignItems="center"
          justifyContent="center"
          boxShadow="10px 10px 20px #ccc"
          padding={2}
          margin="auto"
          marginTop={5}
          borderRadius={5}
          maxWidth={400}
        >
          <Typography
            variant='h4'
            padding={1}
            textAlign="center"
          >{isSignup ? "Sign up" : "Login"}
          </Typography>
          {isSignup && <TextField
            onChange={handleChange}
            value={inputs.name}
            name='name'
            type='text'
            margin='normal'
            placeholder='Name'
          />}
          <TextField
            onChange={handleChange}
            value={inputs.email}
            name='email'
            type='email'
            margin='normal'
            placeholder='Email id'
          />
          <TextField
            onChange={handleChange}
            value={inputs.password}
            name='password'
            type='password'
            margin='normal'
            placeholder='Password'
          />
          <Button
            type='submit'
            variant='contained'
            color='warning'
            sx={{ borderRadius: 3, marginTop: 1 }}
          > Submit
          </Button>
          <Button
            onClick={() => setIsSignup(!isSignup)}
            sx={{ borderRadius: 3, marginTop: 1 }}
          >Change to {isSignup ? "Login" : "Signup"}
          </Button>
        </Box>
      </form>
    </div>
  )
}

export default Auth